section.interior-design-main h3 {
  margin: 0;
}
section.interior-design-main {
  padding-top: 50px;
  padding-bottom: 50px;
  border-top: 1px solid #ccc;
}
.interiorDesignSwiper {
  padding: 0 29px;
  padding-bottom: 15px;
}
.interiorDesignSwiper .swiper-button-prev {
  left: 0;
  background-image: url(images/arrow-left-1.png);
  height: 46px;
  width: 46px;
}
.interiorDesignSwiper .swiper-button-next {
  right: 0;
  background-image: url(images/arrow-right-1.png);
  height: 46px;
  width: 46px;
}
.interiorDesignSwiper .swiper-button-prev:after,
.interiorDesignSwiper .swiper-button-next:after {
  display: none;
}
.interior-d-img {
  line-height: 0;
}
.interior-design-slider {
  max-width: 1435px;
  margin: 0 auto;
  margin-top: 32px;
}
.interior-d-img img {
  width: 100%;
  height: 280px;
  object-fit: cover;
}
section.interior-design-main .interior-design-inner h3 {
  color: #151515;
  font-size: 24px;
  margin-top: 11px;
}
.interior-design-inner a {
  text-decoration: none;
}

.swiper-slide .interior-design-inner h3 {
  transition: all 0.5s ease;
}
.swiper-slide:hover .interior-design-inner h3 {
  color: #ffa824;
  transition: all 0.5s ease;
}

@media (max-width: 1024px) {
  section.interior-design-main h3 {
    font-size: 34px;
  }
  .interiorDesignSwiper {
    padding: 0 15px;
  }
}
