.case-study--inner {
    padding-bottom: 70px;
    padding-top: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.case-study--inner h3 {
    line-height: 1;
    font-size: 40px;
    font-weight: 500;
    margin-bottom: 1rem;
    color: #0e0e0e;
}
.case-study--inner h4 {
    line-height: 1;
    font-size: 22px;
    font-weight: 500;
    margin-bottom: 1rem;
    color: #0e0e0e;
}
.case-study--inner p {
    font-size: 18px;
    color: #606060;
    font-weight: 400;
    line-height: 34px;
}
section.case-study-video .site-title-style {
    margin-top: 70px;
}
section.case-study-content .customized-btn {
    justify-content: center;
    margin-bottom: 70px;
}
.case-study--right {
    width: 47%;
}
.case-study--left {
    width: 55%;
}
section.case-study-content .customized-btn a {
    width: auto;
    padding: 17px 60px;
}
.case-study--inner h3:first-child {
    margin-top: 0;
}
.case-study--right img {
    max-width: 100%;
}
section.case-study-video .blog-banner-sec {
    border-bottom: 0;
}
.site-title-style1 {
    text-align: center;
}
.site-title-style1 h3 {
    font-size: 60px;
    color: #2f2f2f;
    margin-bottom: 0;
}
.site-title-style1 h4 {
    font-size: 28px;
    color: rgb(96, 96, 96);
    margin-top: 10px;
    margin-bottom: 0;
}

/****** Responisve Css *******/

@media(max-width:1400px) {

section.case-study-content .container-size {
    max-width: 94%;
}
.case-study--left {
    width: 48%;
}
.case-study--right {
    width: 48%;
}
.case-study--left video {
    width: 100%;
    height: 345px;
}

}


@media(max-width:1200px) {

.container-size {
    max-width: 95%;
    margin: 0 auto;
}
.case-study--left video {
    width: 100%;
    height: 260px;
}
.site-title-style1 h3 {
    font-size: 50px;
    margin-top: 15px;
}
.site-title-style1 h4 {
    font-size: 22px;
}

}

@media(max-width:780px) {

.case-study--left video {
    width: 100%;
    height: 410px;
}
.case-study--inner {
    display: flex;
    flex-wrap: wrap;
}
.case-study--left {
    width: 100%;
    margin-top: 30px;
}
.case-study--right {
    width: 100%;
    text-align: center;
}
.case-study--right ul {
    text-align: left;
    max-width: 500px;
    margin: 0 auto;
    margin-top: 20px;
}
.case-study--right ul li {
    font-size: 17px;
    line-height: 31px;
}
.case-study--inner {
    padding-bottom: 40px;
    padding-top: 15px;
}
.site-title-style1 h3 {
    font-size: 40px;
}
.site-title-style1 h4 {
    font-size: 18px;
}


}

@media(max-width:640px) {
.case-study--left video {
    height: 300px;
}
}


@media(max-width:480px) {

.case-study--right ul {
    max-width: 85%;
    margin-top: 20px;
}
.case-study--right ul li {
    font-size: 16px;
    line-height: 28px;
}
.case-study--left video {
    height: 219px;
}
.site-title-style1 h3 {
    font-size: 25px;
}
.site-title-style1 h4 {
    font-size: 13px;
}
section.case-study-content .customized-btn {
    justify-content: center;
    margin-bottom: 35px;
}
.case-study--left {
    margin-top: 10px;
}

}