.header-top-section {
  background: #051b31;
  padding: 15px 0;
}
.header-top-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header-top-left p {
  color: #fff;
  line-height: 1;
  font-family: "Barlow", sans-serif;
  font-size: 14px;
}
.header-top-right ul li {
  display: inline-block;
  margin-right: 15px;
  line-height: 1;
}
.header-top-right ul li:last-child {
  margin-right: 0;
}
.header-top-right ul li a {
  color: #fff;
  text-decoration: none;
  font-family: "Barlow", sans-serif;
  font-size: 14px;
  letter-spacing: 1px;
  font-weight: 400;
}
.header-top-right ul li a img {
  line-height: 0;
  display: inline-block;
  margin-right: 5px;
}
.header-top-right ul {
  display: flex;
  align-items: center;
}
.header-top-right ul li:not(:first-child) a img {
  margin-right: 5px;
  position: relative;
  top: 1px;
}
.header-top-right ul li:first-child a img {
  position: relative;
  top: 2px;
}
.header-bottom-section {
  padding: 22px 0;
}
.header-top-right ul li a:hover,
.header-bottom-right ul li a:hover span {
  color: #ffa824;
  transition: 500ms;
}
.header-bottom-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header-bottom-right ul li {
  display: inline-block;
  list-style: none;
  margin-right: 57px;
}
.header-bottom-right ul li:last-child {
  margin-right: 0px;
}
.header-bottom-right ul li a {
  text-decoration: none;
  justify-content: center;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
}
.header-bottom-right ul li a span {
  display: block;
  width: 100%;
  color: #242424;
  line-height: 1;
  margin-top: 7px;
  font-family: "Barlow", sans-serif;
  font-size: 18px;
}

.toggle-bt-mb {
  display: none;
}

.toggle-bt-mb.toggle-close span {
  display: none;
}
.toggle-bt-mb.toggle-close {
  position: relative;
}

.toggle-bt-mb.toggle-close:hover {
  opacity: 1;
}
.toggle-bt-mb.toggle-close:before,
.toggle-bt-mb.toggle-close:after {
  position: absolute;
  left: 20px;
  content: " ";
  height: 25px;
  width: 2px;
  background-color: #ffa824;
  top: 8px;
}
.toggle-bt-mb.toggle-close:before {
  transform: rotate(45deg);
}
.toggle-bt-mb.toggle-close:after {
  transform: rotate(-45deg);
}
.header-bottom-section.fixed {
  position: fixed;
  right: 0;
  left: 0;
  top: 0;
  background: #fffffff2;
  border-bottom: 1px solid #ccc;
  z-index: 11;
}

@media (max-width: 1200px) {
  .header-bottom-section {
    padding: 12px 15px;
  }
  .header-top-section {
    display: none;
  }
  .header-bottom-inner {
    display: block;
  }

  /* navbar-mobile-responsive */

  .header-bottom-right ul li {
    display: flex;
    margin-right: initial;
    width: 100%;
    border-top: 1px solid #d7d7d7a6;
    padding: 15px 20px;
  }

  .header-bottom-right ul li a {
    display: flex;
    width: 100%;
    text-align: left;
    flex-wrap: initial;
  }
  .header-bottom-right ul li a img {
    display: none;
  }
  .header-bottom-right ul li a span {
    margin: 0;
  }

  .header-bottom-left .site-logo {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .toggle-bt-mb {
    width: 45px;
    height: 42px;
    border: 2px solid #ffa824;
    border-radius: 3px;
    padding: 5px;
    cursor: pointer;
    display: block;
  }
  .toggle-bt-mb span {
    width: 100%;
    height: 2px;
    background: #ffa824;
    display: block;
    margin-top: 6.3px;
  }
  .header-bottom-inner .header-bottom-right {
    display: none;
  }

  .header-bottom-right.header-block {
    display: block;
    padding: 5px 0px;
    margin-top: 12px;
    position: absolute;
    background: #fbfbfb;
    width: 100%;
    right: 0;
    left: 0;
    z-index: 11;
   
  }

  .header-bottom-right ul li a span {
    font-size: 16px;
  }
  .header-bottom-right ul li:first-child {
    border-top: 0;
  }

  /* navbar-mobile-responsive */
}
