footer.footer-main-section {
  background-image: url(/src/assets/footer-bg.png);
  background-size: cover;
  background-position: center;
  padding-top: 111px;
}
.footer-inner {
  display: flex;
  justify-content: space-between;
}
.footer-col h3 {
  margin: 0;
  color: #ffa824;
  font-size: 24px;
  font-weight: 400;
  line-height: 1;
  letter-spacing: 1px;
  margin-bottom: 16px;
}
.about-col p {
  color: #fff;
  font-size: 14px;
  font-weight: 400;
  line-height: 30px;
  padding-right: 141px;
}
.about-col {
  width: 31%;
}
.quick-links-col,
.services-col {
  width: 22%;
}
.social-col {
  width: 25%;
}
.footer-col ul li a {
  color: #fff;
  text-decoration: none;
  font-size: 14px;
  line-height: 36px;
  font-weight: 400;
}
.footer-col ul li a:hover,
.social-col p a:hover {
  color: #ffa824;
  transition: 500ms;
}
.ssl-secured {
  margin-top: 20px;
}
.footer-col ul li a i {
  height: 25px;
  width: 25px;
  background: #fff;
  border-radius: 50%;
  color: #000;
  text-align: center;
  line-height: 27px;
  margin-right: 10px;
}
.social-col ul {
  margin-bottom: 20px;
}
.social-col p {
  color: #fff;
  text-decoration: none;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  display: flex;
  align-items: center;
  margin-bottom: 0;
}
.social-col p a {
  text-decoration: none;
  color: #fff;
}
.social-col p:not(:nth-child(4)) img {
  position: relative;
  top: 14px;
}
.copy-right-sec {
  border-top: 1px solid #0f0f0f;
  padding-top: 40px;
  padding-bottom: 40px;
  margin-top: 50px;
}
.copy-right-sec p {
  text-align: center;
  color: #fff;
  font-size: 16px;
  font-weight: 400;
  line-height: 1;
}

ul.social-btn-area li {
  display: flex;
  margin-bottom: 8px;
  align-items: center;
}
ul.social-btn-area {
  margin-top: 25px;
}
ul.social-btn-area li span {
  color: #fff;
}
ul.social-btn-area li a {
  display: flex;
  align-items: center;
  line-height: initial;
}
.footer-col.social-col ul li img {
  margin-top: 7px;
}
.payments {
  line-height: 0;
  margin-top: 0;
}
.payments img {
  max-width: 250px;
}


.payments ul {
  display: flex;
  list-style: none;
  margin-bottom: 0;
}
.payments ul li {
  padding: 0 5px;
}
.payments ul li img {
  width: 75px;
}
.footer-col.social-col ul li img {
  margin-top: 7px;
}

@media (min-width: 1024px) and (max-width: 1440px) {
  footer.footer-main-section {
    padding: 50px 30px 0px;
  }
  .about-col p {
    padding-right: 30px;
  }
}
@media (max-width: 1024px) {
  .footer-inner {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .footer-inner .footer-col {
    width: 33%;
  }
  .about-col p {
    line-height: 26px;
    padding-right: 0;
  }
  .footer-inner .footer-col {
    width: 30%;
    margin: 30px 15px;
  }
  .copy-right-sec p {
    font-size: 16px;
  }
  .copy-right-sec {
    margin-top: 25px;
    padding-bottom: 5px;
  }
  .customized-btn a {
    width: 200px;
  }
  footer.footer-main-section {
    padding: 50px 30px 30px;
  }
}

@media (max-width: 768px) {
  .footer-inner {
    flex-wrap: wrap;
  }

  footer.footer-main-section {
    padding: 50px 15px 30px;
  }

  .footer-col h3 {
    text-align: center;
  }
  .about-col p {
    padding-right: inherit;
    text-align: center;
    line-height: 24px;
    font-size: 14px !important;
  }
  .ssl-secured img {
    width: 150px;
  }
  .footer-col ul,
  .ssl-secured {
    text-align: center;
  }

  .footer-col.social-col ul {
    display: flex;
    justify-content: center;
    margin-top: 25px;
  }
  .footer-col.social-col ul {
    display: flex;
    justify-content: center;
    margin-top: 25px;
    flex-wrap: wrap;
  }
  .footer-col.social-col ul li {
    margin: 0 15px;
  }
  .footer-col ul li a {
    font-size: 14px;
    line-height: 30px;
  }
  .copy-right-sec {
    margin-top: 15px;
    padding-top: 30px;
    padding-bottom: 0;
  }
  .copy-right-sec p {
    line-height: 28px;
  }

  .footer-col.social-col ul.social-btn-area {
    margin-top: 0;
  }
  .footer-inner .footer-col {
    width: 100%;
    margin: 30px 15px;
    margin-bottom: 0;
  }
  .customized-btn a {
    width: 160px;
  }
  .payments ul li {
    margin: 0 0px !important;
} 
}
