section.categories-main {
  background: #f7f7f7;
  padding-top: 60px;
  padding-bottom: 70px;
}
h3.categories-title-p {
  margin-bottom: 38px;
}
.categoriesSwiper {
  padding: 0 27px;
}
.categoriesSwiper .swiper-button-prev {
  left: 0;
  background-image: url(images/arrow-left-1.png);
  height: 46px;
  width: 46px;
}
.categoriesSwiper .swiper-button-next {
  right: 0;
  background-image: url(images/arrow-right-1.png);
  height: 46px;
  width: 46px;
}
.categoriesSwiper .swiper-button-prev:after,
.categoriesSwiper .swiper-button-next:after {
  display: none;
}
.categories-slider {
  max-width: 1415px;
  margin: 0 auto;
}
.categories-slider .categoriesSwiper {
  padding: 0 27px;
}
.categories-img {
  line-height: 0;
}
.categories-img img {
  width: 100%;
  height: 428px;
  object-fit: cover;
}
.categories-inner h3 {
  text-align: center;
  color: #151515;
  font-weight: 400;
  margin: 0;
  line-height: 1.5;
  font-size: 24px;
  margin-top: 13px;
}
.categories-inner a {
  text-decoration: none;
}
.swiper-slide:hover .categories-inner h3 {
  transition: all 0.5s ease;
}
.swiper-slide:hover .categories-inner h3 {
  color: #ffa824;
  transition: all 0.5s ease;
}

@media (max-width: 1024px) {
  .categories-img img {
    height: 380px;
  }
  .categories-slider .categoriesSwiper {
    padding: 0 15px;
  }
}
