.about-us-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.about-us-right {
  width: 50%;
  text-align: right;
}
.about-us-left {
  width: 50%;
}
section.about-us-section {
  padding: 70px 0;
}
.about-us-left h1 {
  font-weight: bold;
  color: #000000;
  font-size: 55px;
  margin: 0;
  line-height: 1;
  margin-bottom: 20px;
  font-weight: 400;
}
.about-us-left p {
  color: #494949;
  font-size: 18px;
  line-height: 30px;
  margin-bottom: 18px;
}
.about-us-right img {
  box-shadow: 0px 3px 5px rgb(0 0 0 / 12%);
  background: #fff;
  padding: 15px 15px;
}

section.terms-us-section {
  padding: 70px 15px;
}

@media (min-width: 769px) and (max-width: 1440px) {
  section.terms-us-section {
    padding: 50px 30px;
  }
  section.terms-us-section .text-body-container h3 {
    font-size: 36px;
  }
}

@media (max-width: 768px) {
  section.whytogo-main {
    padding-top: 0px !important;
  }
  section.about-us-section,
  section.whytogo-main {
    padding: 50px 15px;
  }
  .about-us-inner,
  .whytogo-inner {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }
  .about-us-left,
  .whytogo-left {
    width: 100%;
    order: 2;
  }
  .about-us-right,
  .whytogo-right {
    width: 100%;
    order: 1;
  }
  .about-us-right img {
    width: 100%;
    height: 290px;
  }
  .about-us-left h1 {
    margin-bottom: 15px;
    font-size: 36px;
    margin-top: 25px;
    text-align: center;
  }
  section.whytogo-main {
    padding-top: 0;
  }
  section.about-us-section p,
  .whytogo-inner.affordable-inner p,
  .whytogo-inner.swift-inner p,
  .whytogo-inner.accessible-inner p,
  .whytogo-inner.quality-inner P {
    text-align: center;
  }
  .whytogo-content h3 {
    font-size: 36px;
    text-align: center;
  }
  .whytogo-title h3 {
    font-size: 36px;
  }
  .whytogo-content p {
    line-height: 28px;
  }
  .whytogo-inner.affordable-inner .whytogo-left,
  .whytogo-inner.accessible-inner .whytogo-left,
  .whytogo-inner.swift-inner .whytogo-left,
  .whytogo-inner.quality-inner .whytogo-left {
    order: 1;
    margin-bottom: 30px;
  }
  section.whytogo-main .whytogo-title h3 {
    margin-bottom: 35px;
  }

  section.terms-us-section {
    padding: 50px 15px;
  }
  section.terms-us-section .text-body-container h3 {
    text-align: center;
    font-size: 36px;
  }
  section.terms-us-section .text-body-container p {
    text-align: center;
  }
}
