table.table-payment {
    max-width: 580px;
    margin: auto;
    width: 100%;
    border-collapse: collapse;
    /* box-shadow: 0px 2px 10px 0px rgb(0 0 0 / 52%); */
    transform: translateY(30px);
}

table.table-payment tbody tr td:first-child {
    font-weight: bold;
    text-transform: uppercase;
}
table.table-payment tbody tr td {
    border: 1px solid #dee2e6;
    padding: 14px 15px;
}
.qr-wrap {
    max-width: 1000px;
    margin: auto;
    display: flex;
    justify-content: space-between;
}
.qr-wrap .qr-inner img {
    width: 100%;
}
.qr-wrap .heading-plan {
    max-width: 100%;
}
.qr-wrap .heading-plan h1 {
    margin-bottom: 10px;
}
.qr-inner, .payment-right-sec {
    width: 48%;
}
.payments-page {
    padding-top: 70px;
    text-align: center;
}
.payments-page img {
    border: 1px solid #ccc;
    display: block;
    text-align: center;
    margin: auto;
    margin-top: 40px;
    margin-bottom: 30px;
}
.payments-page a {
    display: inline-block;
    background: #ffa824;
    padding: 18px 43px;
    text-decoration: none;
    color: #fff;
    line-height: 1;
    font-family: "Mukta", sans-serif;
    font-size: 19px;
    font-weight: 500;
}


@media(max-width:780px){

.qr-wrap {
    display: block;
    max-width: 95%;
}
.qr-inner, .payment-right-sec {
    width: 100%;
}
.heading-plan h1 {
    font-size: 25px;
}
.interiors-list-sec {
    padding: 40px 0;
    border-top: 1px solid #ccc;
}
.payments-page a {
    padding: 10px 35px;
    font-size: 19px;
}
table.table-payment tbody tr td {
    padding: 10px 15px;
    font-size: 15px;
}

}