ul.postimg-list {
    display: flex;
    flex-wrap: wrap;
}
ul.postimg-list li {
    width: 100%;
    max-width: 33%;
    list-style: none;
    padding: 10px;
    margin-bottom: 40px;
}
ul.postimg-list img {
    width: 100%;
    max-width: 100%;
    height: 310px;
    object-fit: cover;
}
section.interior-design-main h1 {
    text-align: center;
    color: #2e2e2e;
    font-weight: bold;
    margin: 0;
    line-height: 1;
    font-size: 36px;
}
section.interior-design-main h1 {
    text-transform: capitalize;
}
ul.postimg-list li a h3 {
    text-align: center;
    font-size: 22px;
    margin-top: 11px;
    font-weight: 400;
    text-decoration: none;
    color: #151515;
}
ul.postimg-list li a {
    text-decoration: none;
}