.blog-single-inner {
    padding-top: 40px;
    padding-bottom: 70px;
}
.blog-single-inner h3 {
    width: 100%;
    margin: 0 0px 1rem;
    font-size: 28px;
    font-weight: 600;
    color: #001f33;
}
section.blog-single-main .container-size {
    max-width: 940px;
}
.blog-single-image {
    line-height: 0;
    margin-bottom: 20px;
}
.blog-single-content p {
    color: #494949;
    font-size: 18px;
    line-height: 27px;
    margin-bottom: 20px;
}
.blog-single-content p:last-child {
    margin-bottom: 0;
}
.blog-single-content ul {
    padding-left: 18px;
}
.blog-single-content ul li {
    color: #494949;
    font-size: 18px;
    line-height: 27px;
    margin-bottom: 10px;
}
.blog-single-content p a {
    color: #ffa824;
    text-decoration: none;
}
.blog-single-image img, .blog-single-content img {
    max-width: 100%;
}

@media(max-width:1200px) {

section.blog-single-main .container-size {
    max-width: 94%;
}
.blog-single-inner h3 {
    font-size: 22px;
}

}