.get-in-touch-img {
  line-height: 0;
  width: 68.5%;
}
section.get-in-touch-main {
  display: flex;
}
.get-in-touch-img img {
  width: 100%;
}

.get-in-touch-form form button:hover {
  cursor: pointer;
}
.Success-msg {
  color: #fff;
  background: #71d80c;
  line-height: 1;
  padding: 7px;
  margin-top: 10px;
  transition: 700ms;
}

@media (min-width: 769px) and (max-width: 1366px) {
  .get-in-touch-img img {
    height: 100vh;
  }
  .get-in-touch-form {
    padding: 50px 40px;
  }
}

@media (max-width: 768px) {
  .get-in-touch-img {
    line-height: 0;
    width: 100%;
  }
  section.get-in-touch-main {
    display: flex;
    flex-wrap: wrap;
  }
  .get-in-touch-form form {
    display: flex;
    flex-wrap: wrap;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-right: 10px;
    margin-left: 10px;
  }
  .get-in-touch-form {
    padding: 50px 10px;
    margin: auto;
    margin-top: 50px;
    margin-bottom: 50px;
  }
  .get-in-touch-form form .col-md-6 {
    -ms-flex-preferred-size: 0;
    flex-basis: 0;
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    max-width: 100%;
  }
  .get-in-touch-img img {
    width: 100%;
  }
}
@media (max-width: 580px) {
    .get-in-touch-form {
        margin-left: 15px;
        margin-right: 15px;
    }
}
