.text-body-container h3 {
  font-weight: bold;
  color: #000000;
  font-size: 55px;
  margin: 0;
  line-height: 1;
  margin-bottom: 20px;
  font-weight: 400;
}
.text-body-container p {
  color: #494949;
  font-size: 18px;
  line-height: 30px;
  margin-bottom: 18px;
}
section.privacy-us-section {
  padding: 70px 15px;
}

@media (min-width: 769px) and (max-width: 1440px) {
  section.privacy-us-section {
    padding: 50px 30px;
  }
  section.privacy-us-section .text-body-container h3 {
    font-size: 36px;
  }
}

@media (max-width: 768px) {
  section.privacy-us-section {
    padding: 50px 15px;
  }

  section.privacy-us-section .text-body-container h3 {
    text-align: center;
    font-size: 36px;
  }
  section.privacy-us-section .text-body-container p {
    text-align: center;
  }
}
