section.home-banner-main {
  padding-top: 227px;
  padding-bottom: 135px;
  background-size: cover;
  background-position: center;
  background-image: url(https://dreamhousemakerz.com/production/wp-content/uploads/2022/07/home-banner-1.png);
}
.home-banner-inner {
  text-align: center;
}
.home-banner-inner p {
  color: #ffffff;
  font-size: 22px;
  font-family: "Barlow", sans-serif;
  font-weight: 500;
  line-height: 1;
  margin-bottom: 9px;
}
.home-banner-inner h1 {
  color: #fff;
  font-size: 90px;
  margin: 0;
  line-height: 1;
  font-family: "Barlow", sans-serif;
  font-weight: 600;
  letter-spacing: normal;
}
.home-banner-inner h2 {
  margin: 0;
  color: #fff;
  font-size: 38px;
  margin-bottom: 18px;
  font-family: "Barlow", sans-serif;
  font-weight: 500;
  margin-top: 24px;
}
.home-banner-inner a {
  display: inline-block;
  background: #ffa824;
  padding: 18px 43px;
  text-decoration: none;
  color: #fff;
  line-height: 1;
  font-family: "Mukta", sans-serif;
  font-size: 19px;
  font-weight: 500;
}


.banner-full .container-heading {
  position: relative;
}
.banner-full .container-heading img {
  width: 100%;
  height: 600px;
  object-fit: cover;
}
.banner-full .container-heading .heading-box {
  position: absolute;
  top: 57%;
  right: 0;
  left: 0;
  margin: auto;
  text-align: center;
}
.banner-full .container-heading .heading-box span {
  color: #ffffff;
  font-size: 22px;
  font-family: "Barlow", sans-serif;
  font-weight: 500;
  line-height: 1;
  margin-bottom: 9px;
  display: inline-block;
}
.banner-full .container-heading .heading-box h1 {
    color: #fff;
    font-size: 50px;
    margin: 0;
    line-height: 1;
    font-family: "Barlow", sans-serif;
    font-weight: 600;
    letter-spacing: normal;
}
.banner-full .container-heading .heading-box p {
    margin: 0;
    color: #fff;
    font-size: 25px;
    margin-bottom: 18px;
    font-family: "Barlow", sans-serif;
    font-weight: 500;
    margin-top: 24px;
} 

.banner-full .container-heading .heading-box a {
   display: inline-block;
    background: #ffa824;
    padding: 18px 43px;
    text-decoration: none;
    color: #fff;
    line-height: 1;
    font-family: "Mukta", sans-serif;
    font-size: 19px;
    font-weight: 500;
}

.banner-full .container-heading .heading-box:hover a {
  background: #04748e;
  transition: all 0.5s ease;
}
.swiper-wrap .swiper-slide {
  opacity: 1;
}
.swiper-wrap .swiper-button-prev, .swiper-wrap .swiper-button-next {
  background: #ffa824;
  width: 50px;
  height: 50px;
  border-radius: 3px;
}
.swiper-wrap .swiper-button-prev::after, .swiper-wrap .swiper-button-next::after {
  color: #fff;
  font-size: 20px;
}
.swiper-wrap .swiper-slide {
  opacity: 1 !important;
}

.banner-full .container-heading::before {
  position: absolute;
  content: '';
  background: #0000007a;
  top: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 100%;
}


@media (max-width: 1700px){
  .banner-full .container-heading img {
    height: 500px;
  }
}

@media (min-width: 1025px) and (max-width: 1440px) {
  section.home-banner-main {
    padding: 100px 0;
  }
  .home-banner-inner h1 {
    font-size: 50px;
    line-height: 1.2;
  }
  .home-banner-inner a {
    padding: 15px 35px;
    font-size: 18px;
  }
  .home-banner-inner h2 {
    font-size: 30px;
    max-width: 350px;
    margin: auto;
    margin-top: 15px;
    margin-bottom: 15px;
}
.header-top-section, .header-bottom-section {
    padding: 15px 30px;
}
}

@media (max-width: 1024px) {
  .home-banner-inner h1, .banner-full .container-heading .heading-box h1 {
    font-size: 38px;
    line-height: 1.2;
  }
  .home-banner-inner h2, .banner-full .container-heading .heading-box p {
    font-size: 20px;
    max-width: 300px;
    margin: auto;
    margin-top: 15px;
    margin-bottom: 15px;
  }
  section.home-banner-main {
    padding: 80px 5px;
  }
  .home-banner-inner a, .banner-full .container-heading .heading-box a {
    padding: 15px 35px;
    font-size: 18px;
  }

  .home-section-two-inner {
    display: flex;
    flex-wrap: wrap;
    padding: 0 15px;
  }
  .home-section-two-inner .home-two-list {
    width: 46%;
    margin: 5px 5px;
  }
  .home-two-list .home-two-img img {
    max-width: 60px;
    width: 100%;
  }

  .banner-full .container-heading img {
    height: 450px;
    object-fit: cover;
}  
.banner-full .container-heading .heading-box {
  top: 25%;
}
how-it-works-steps {
  width: 46%;
  margin: 0 5px;
  margin-bottom: 25px;
}

}
