.blog-banner-sec img {
    width: 100%;
}
.site-title-style h1 {
    display: inline-block;
    background: #ffa824;
    color: #fff;
    padding: 6px 40px;
    padding-bottom: 10px;
    font-size: 35px;
    font-weight: 600;
    margin: 0;
}
.blog-banner-sec {
    border-bottom: 1px solid #ededed;
}
section.blog-banner-main .site-title-style {
    padding-top: 50px;
    padding-bottom: 50px;
}
.blog-list-inner {
    display: flex;
    flex-wrap: wrap;
    margin-left: -15px;
    margin-right: -15px;
}
.blog-list-inner .blog-list {
    width: 30%;
    box-shadow: 1px 3px 7px 3px rgb(0 0 0 / 12%);
    margin: 1%;
}
.blog-list .blog-image img {
    max-width: 100%;
    height: 100%;
    object-fit: cover;
    transition: all 0.7s ease;
    width: 100%;
}
section.blog-list-main {
    padding-bottom: 70px;
}
.blog-content a {
    display: inline-block;
    text-decoration: none;
    color: #fff;
    font-size: 16px;
    max-width: 140px;
    text-align: center;
    background: #ffa824;
    padding: 10px 0;
    font-family: "Mukta", sans-serif;
    line-height: 1;
    font-weight: 500;
    width: 100%;
}
.blog-content a:hover {
    background: #001f33;
    transition: 500ms;
}
.blog-image {
    height: 275px;
    overflow: hidden;
    line-height: 0;
}
.blog-content h3 {
    color: #001f33;
    margin: 0;
    margin-bottom: 15px;
}
.blog-content {
    padding: 20px;
}
.blog-list .blog-image img:hover {
    transition: all 0.7s ease;
    transform: scale(1.5);
}
.blog-excerpt p {
    color: #494949;
    font-size: 18px;
    margin-bottom: 18px;
    line-height: 25px;
}
section.blog-banner-main {
    border-top: 1px solid #ccc;
}

/***** Responsive *****/

@media(max-width:1200px) {

.blog-image {
    height: 200px;
    overflow: hidden;
    line-height: 0;
}
section.blog-list-main .container-size {
    max-width: 95%;
}

}

@media(max-width:780px) {

.blog-image {
    height: 140px;
}

}

@media(max-width:767px) {

.blog-list-inner .blog-list {
    width: 100%;
    margin-bottom: 25px;
}
.blog-list .blog-image img {
    width: 100%;
}
.blog-image {
    height: auto;
}

}