.how-it-works-top {
    background: #051b31;
    text-align: center;
    padding-top: 65px;
    padding-bottom: 126px;
}
.how-it-works-top h3 {
    margin: 0;
    line-height: 1;
    color: #fff;
    font-size: 36px;
    font-weight: 400;
}
.how-it-works-bottom {
    display: flex;
    justify-content: space-between;
    text-align: center;
    margin-top: -86px;
    position: relative;
    z-index: 1;
    padding-bottom: 82px;
}
.how-it-works-steps {
    width: 19%;
}
.how-it-works-steps h3 {
    margin: 0;
    background: #04748e;
    color: #fff;
    font-weight: 400;
    line-height: 1;
    padding: 10px 0;
    font-size: 18px;
    border-radius: 5px 5px 0px 0px;
}
.works-steps-img {
    min-height: 190px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 3px 5px rgb(0 0 0 / 12%);
    background: #fff;
}
.how-it-works-steps p {
    font-size: 18px;
    color: #292929;
    font-weight: 400;
    line-height: 24px;
    margin-top: 12px;
}


@media (min-width: 768px) and (max-width: 1440px){
    .how-it-works-bottom {
        padding: 0 30px;
        padding-bottom: 70px;
    }
}

@media (max-width: 768px){
    .how-it-works-bottom {
        display: flex;
        flex-wrap: wrap;
        padding: 0 15px;
        padding-bottom: 50px;
    }
    .how-it-works-steps {
        width: 47%;
        margin: 0 5px;
        margin-bottom: 25px;
    }
    .works-steps-img {
        min-height: 150px;
    }
    .works-steps-img img {
        max-width: 60px;
        width: 100%;
    }
    .how-it-works-top h3 {
        font-size: 34px;
    }  
}

@media (max-width: 365px){
    .how-it-works-steps {
        width: 100%;
    }
}