.about-us-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.about-us-right {
  width: 50%;
  text-align: right;
}
.about-us-left {
  width: 50%;
}
section.about-us-section {
  padding: 70px 0;
}
.about-us-left h1 {
  font-weight: bold;
  color: #000000;
  font-size: 55px;
  margin: 0;
  line-height: 1;
  margin-bottom: 20px;
  font-weight: 400;
}
.about-us-left p {
  color: #494949;
  font-size: 18px;
  line-height: 30px;
  margin-bottom: 18px;
}
.about-us-right img {
  box-shadow: 0px 3px 5px rgb(0 0 0 / 12%);
  background: #fff;
  padding: 15px 15px;
}

@media (min-width: 1024px) and (max-width: 1440px) {
  .about-us-right img {
    width: 100%;
  }
  section.about-us-section,
  section.whytogo-main {
    padding: 50px 30px !important;
  }
  section.about-us-section .about-us-right,
  .whytogo-inner.swift-inner .whytogo-left,
  .whytogo-inner.quality-inner .whytogo-left {
    padding-left: 30px;
  }
  .whytogo-inner.affordable-inner .whytogo-left,
  .whytogo-inner.accessible-inner .whytogo-left {
    padding-right: 30px;
  }
  .whytogo-title h3,
  .whytogo-content h3,
  section.about-us-section .about-us-left h1 {
    font-size: 36px !important;
  }
}


@media (max-width: 1024px) {
  .whytogo-title h3,
  .whytogo-content h3,
  section.about-us-section .about-us-left h1 {
    font-size: 36px !important;
  }
  section.about-us-section,
  section.whytogo-main {
    padding: 50px 30px !important;
  }
  section.about-us-section .about-us-right,
  .whytogo-inner.swift-inner .whytogo-left,
  .whytogo-inner.quality-inner .whytogo-left {
    padding-left: 30px;
  }
  .whytogo-inner.affordable-inner .whytogo-left,
  .whytogo-inner.accessible-inner .whytogo-left {
    padding-right: 30px;
  }
  .whytogo-title h3 {
    font-size: 36px !important;
  }
  .about-us-right img {
    width: 100%;
  }
}

@media (max-width: 768px) {
    section.about-us-section .about-us-right,
    .whytogo-inner.swift-inner .whytogo-left,
    .whytogo-inner.quality-inner .whytogo-left {
      padding-left: 0px;
    }
    .whytogo-inner.affordable-inner .whytogo-left,
    .whytogo-inner.accessible-inner .whytogo-left {
      padding-right: 0px;
    }
    .whytogo-content h3 {
        font-size: 36px !important;
        margin-bottom: 15px !important;
    }
}