section.client-says-main {
    background: #effdff;
    padding-top: 100px;
    padding-bottom: 112px;
}
.client-says-size {
    max-width: 1310px;
    margin: 0 auto;
}
.client-says-inner {
    max-width: 885px;
    margin: 0 auto;
    text-align: center;
}
.client-says-inner h3 {
    color: #191919;
    font-size: 48px;
    font-weight: 400;
    line-height: 1;
    margin-top: 39px;
    margin-bottom: 28px;
}
.client-says-inner p, .client-says-inner h4 {
    color: #191919;
    font-size: 24px;
    font-weight: 400;
    line-height: 36px;
}
.client-says-inner h4 {
    margin: 0;
    color: #1d1d1d;
    margin-top: 22px;
}
.client-says-size .swiper-button-prev::after, .client-says-size .swiper-button-next::after {
    display: none;
}
.client-says-size .swiper-button-prev {
    background-image: url(images/arrow-left.png);
}
.client-says-size .swiper-button-next {
    background-image: url(images/arrow-right.png);
}
.client-says-size .mySwiper .swiper-slide {
    opacity: 1;
}
section.client-says-main iframe {
    border: 0;
}
.reviews-iframe {
    max-width: 1400px;
    margin: 0 auto;
}

@media (min-width: 1025px) and (max-width: 1440px) {
    .client-says-inner h3 {
        font-size: 36px;
        margin-bottom: 15px;
    }
    .client-says-inner p, .client-says-inner h4 {
        font-size: 22px;
    } 
}


@media (max-width: 1024px){
    .client-says-inner h3 {
        font-size: 34px;
        margin: 15px 0;
    }
    .client-says-inner img {
        max-width: 40px;
    }
    .client-says-inner h4 {
        padding: 0 15px;
        font-size: 16px;
        line-height: 26px;
    }

    section.client-says-main {
        background: #effdff;
        padding: 80px 15px;
    }  

}