section.complete-customized-main {
  background-size: cover;
  background-position: center;
  padding-top: 192px;
  padding-bottom: 136px;
}
.complete-customized-inner {
  text-align: center;
}
.complete-customized-inner h3 {
  margin: 0;
  color: #fff;
  font-size: 60px;
  font-weight: 400;
  line-height: 66px;
  margin-bottom: 29px;
}
.complete-customized-inner h3 strong {
  font-weight: bold;
  color: #ffa824;
  font-size: 69px;
}
.customized-btn {
  display: flex;
  justify-content: space-between;
  max-width: 422px;
  margin: 0 auto;
}
.customized-btn a {
  display: inline-block;
  text-decoration: none;
  color: #fff;
  font-size: 19px;
  width: 197px;
  text-align: center;
  background: #ffa824;
  padding: 17px 0;
  font-family: "Mukta", sans-serif;
  line-height: 1;
  font-weight: 500;
  transition: all 0.5s ease;
}

.customized-btn .customized-btn-right a {
  background: #04748e;
}

section.archtecture_visual_art {
  background: #f0f0f0;
  padding: 50px 15px;
}
section.archtecture_visual_art .heading-container-wrap h2 {
  line-height: 1;
  color: #000;
  font-size: 30px;
  font-weight: 500;
  margin-bottom: 1rem;
  margin-top: 0;
}
section.archtecture_visual_art .heading-container-wrap p {
  color: #494949;
  font-size: 16px;
  line-height: 25px;
  max-width: 1140px;
  margin: auto;
  margin-bottom: 15px;
}
section.archtecture_visual_art .heading-container-wrap p:last-child {
  margin-bottom: 0;
}

section.archtecture_visual_art .heading-container-wrap {
  text-align: center;
}
@media (min-width: 1025px) and (max-width: 1440px) {
  .complete-customized-inner h3 {
    font-size: 30px;
    line-height: 1.2;
  }
  .complete-customized-inner h3 strong {
    font-size: 36px;
    display: block;
    margin-bottom: 5px;
  }
  .complete-customized-inner h3 br {
    display: none;
  }
}
@media (max-width: 1024px) {
  section.complete-customized-main {
    padding: 80px 15px;
  }
  .complete-customized-inner h3 br {
    display: none;
  }
  .complete-customized-inner h3 strong {
    font-size: 34px;
    display: block;
    margin-bottom: 5px;
  }
  .complete-customized-inner h3 {
    font-size: 25px;
    line-height: 1.2;
  }
  .customized-btn a {
    font-size: 16px;
    width: 205px;
  }
  .customized-btn {
    display: flex;
    justify-content: center;
    max-width: 455px;
    flex-wrap: wrap;
  }
  .customized-btn a {
    margin: 0px 10px;
  }
  h3.categories-title-p {
    font-size: 34px;
  }
  .customized-btn .customized-btn-left {
    margin-bottom: 15px;
  }
}

@media (max-width: 767px) {

section.archtecture_visual_art {
  padding: 30px 15px;
}
section.archtecture_visual_art .heading-container-wrap h2 {
  font-size: 22px;
}
section.archtecture_visual_art .heading-container-wrap p {
  font-size: 14px !important;
  line-height: 19px;
  margin-bottom: 10px;
}

}