.drawings-banner-sec img {
    width: 100%;
}
.drawings-banner-sec{
    position: relative;
}

.drawings-heading-area {
    position: absolute;
    top: 28%;
    max-width: 1415px;
    margin: auto;
    right: 0;
    left: 0;
}
.head-inner P {
    font-family: 'Barlow';
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0.185em;
    text-transform: uppercase;
    color: #2E2E2E;
}
.head-inner h1 {
    font-family: 'Barlow';
    font-style: normal;
    font-weight: 600;
    font-size: 66px;
    line-height: 0px;
    text-transform: capitalize;
    color: #2E2E2E;
    margin-bottom: 4.4rem;
    margin-top: 3rem;
}
.drawings-heading-area a {
    display: inline-block;
    background: #ffa824;
    padding: 18px 43px;
    text-decoration: none;
    color: #fff;
    line-height: 1;
    font-family: 'Mukta', sans-serif;
    font-size: 19px;
    font-weight: 500;
}
.drawings-heading-area .head-inner {
    max-width: 563px;
    text-align: center;
}   

section.drawings-sen-one, section.drawings-sen-two {
    padding: 70px 0;
}


.stap-wrap-row {
    display: flex;
    flex-wrap: wrap;
}
.stap-wrap-row .step-inner {
    width: 100%;
    max-width: 33.3%;
    padding: 0 15px;
}
.stap-wrap-row .step-inner .step-gl-img img {
    width: 100%;
    height: 300px;
}


.stap-wrap-row .step-inner:nth-child(2) {
    border-right: 2px solid #2e2e2e7d;
    border-left: 2px solid #2e2e2e7d;
    padding-top: 50px;
}
.step-gl-img {
    position: relative;
}
.step-gl-img::before {
    position: absolute;
    content: '';
    border-bottom: 2px solid #2e2e2e7d;
    right: 0;
    left: 0;
    bottom: -20px;
    margin: 0 -15px;
}


.stap-wrap-row:last-child .step-inner:nth-child(2) .step-gl-img::before {
    display: none;
}
.step-inner .step-head {
    text-align: center;
    margin-bottom: 60px;
    position: relative;
}
.step-inner .step-head h2 {
    width: 40px;
    height: 40px;
    margin: auto;
    border: 1px solid #ddd;
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Barlow';
    font-style: normal;
    font-weight: 700;
    font-size: 22px;
    line-height: 26px;
    text-transform: capitalize;
}

.step-inner .step-head h3 {
    font-family: 'Barlow';
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    text-align: center;
    text-transform: capitalize;
    margin: 12px 0 0;
}


