/* color-css */

/* arrow-down */

.step-head.pinc-cl::before, .step-head.violet-cl::before, .step-head.orang-cl::before, .step-head.purple-cl::before, .step-head.green-cl::before{
    position: absolute;
    content: '';
    width: 2px;
    right: 0;
    left: 0;
    height: 70px;
    margin: auto;
    bottom: -6em;
    z-index: 1;
}

.step-head.pinc-cl::after, .step-head.violet-cl::after, .step-head.orang-cl::after, .step-head.purple-cl::after, .step-head.green-cl::after {
    position: absolute;
    content: '';
    width: 5px;
    height: 5px;
    transform: rotate(45deg);
    right: 0;
    left: 0;
    margin: auto;
    bottom: -6em;
    z-index: 1;
    border-top: 0px !important;
    border-left: 0px !important;
}

.step-head.pinc-cl::before {
    background: #EF399A;
}
.step-head.pinc-cl::after {
    border: 1px solid #EF399A;
  
}

.step-head.violet-cl::before {
    background: #3678C0;
}
.step-head.violet-cl::after {
    border: 1px solid #3678C0;
  
}

.step-head.orang-cl::before {
    background: #F37010;
}
.step-head.orang-cl::after {
    border: 1px solid #F37010;
  
}
.step-head.purple-cl::before {
    background: #603A8A;
}
.step-head.purple-cl::after {
    border: 1px solid #603A8A;
  
}

.step-head.green-cl::before {
    background: #23A859;
}
.step-head.green-cl::after {
    border: 1px solid #23A859;
  
}


