.who-we-are-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.who-we-are-img img {
  max-width: 100%;
  padding: 0 15px;
}
section.who-we-are-main .container-size {
  max-width: 1360px;
}
.who-we-are-left h3 {
  color: #2d2d2d;
  margin: 0;
  line-height: 1;
  font-size: 60px;
  font-weight: 400;
  margin-bottom: 26px;
}
.who-we-are-left {
  width: 57%;
  padding-top: 52px;
}
.who-we-are-right {
  width: 37%;
  text-align: right;
}
section.who-we-are-main {
  padding-top: 115px;
  padding-bottom: 125px;
}
.who-we-are-img {
  box-shadow: 0px 3px 5px rgb(0 0 0 / 12%);
  background: #fff;
  text-align: center;
  line-height: 0;
  padding: 15px 0;
}
.who-we-are-left p {
  color: #494949;
  font-size: 20px;
  line-height: 34px;
}
.who-we-are-counter {
  display: flex;
  justify-content: space-between;
}
.who-we-counter-list {
  width: 30%;
  text-align: center;
  box-shadow: 0px 3px 5px rgb(0 0 0 / 12%);
  background: #fff;
  padding-top: 19px;
  padding-bottom: 29px;
}
.who-we-counter-list h3 {
  margin: 0;
  color: #04748e;
  font-size: 46px;
  line-height: 1;
  font-weight: 400;
}
.who-we-counter-list p {
  line-height: 1;
  color: #202020;
  font-size: 18px;
  font-weight: 400;
  margin-top: 5px;
}
.who-we-are-left p.who-we-are-des {
  padding-right: 75px;
  margin-bottom: 39px;
}

ul.list-who_are {
  margin-left: 25px;
  margin-bottom: 40px;
}
ul.list-who_are li {
  color: #494949;
  font-size: 18px;
  line-height: 32px;
  font-family: 'Barlow', sans-serif;
}
.who-we-are-counter.who-we-are-counter-b {
  justify-content: center;
  margin-top: 19px;
}
.who-we-are-counter.who-we-are-counter-b .who-we-counter-list {
  margin: 0 20px;
}

@media (min-width: 1025px) and (max-width: 1440px) {
.who-we-are-img img {
  width: 100%;
}
section.who-we-are-main {
  padding: 50px 30px;
}
.who-we-are-left p {
  line-height: 36px;
}
.who-we-are-left {
  padding-top: 0;
}
.who-we-are-left h3 {
  font-size: 36px;
  margin-bottom: 15px;
}
.who-we-are-inner.who-inner-wrap {
  align-items: center;
}
}

@media (max-width: 1024px) {
  .who-we-are-img {
    padding: 15px 0;
  }
  .who-we-are-img img {
    width: 100%;
    padding: 0 15px;
  }

  .who-we-are-inner {
    display: flex;
    flex-wrap: wrap;
  }
  .who-we-are-left {
    width: 100%;
    order: 2;
  }
  .who-we-are-right {
    width: 100%;
    order: 1 !important;
  }
  section.who-we-are-main {
    padding: 50px 15px;
  }
  .who-we-are-left h3 {
    font-size: 34px;
    margin-bottom: 15px;
    text-align: center;
  }
  .who-we-are-left p.who-we-are-des {
    text-align: center;
    line-height: 28px;
    font-size: 16px;
    padding: initial;
  }
  .who-we-counter-list {
    padding: 20px;
  }
  .who-we-are-left h3 {
    font-size: 24px;
  }
  .who-we-counter-list p {
    line-height: 18px;
    font-size: 14px;
  }
  .who-we-are-img {
    max-width: 300px;
    margin: auto;
  }

}
