section.project-completed-main {
  background: #f4faf0;
  padding-top: 50px;
  padding-bottom: 60px;
}
.completed-title h3 {
  margin: 0;
  text-align: center;
  color: #2e2e2e;
  font-size: 36px;
  font-weight: 500;
  margin-bottom: 50px;
}
.completed-inner {
  background: #fff;
  text-align: center;
  min-height: 250px;
  border-radius: 10px;
}
.project-completed-size {
  max-width: 1775px;
  margin: 0 auto;
  position: relative;
}
.completed-img {
  min-height: 155px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.completed-inner h3 {
  margin: 0;
  font-size: 24px;
  font-weight: 600;
  color: #2e2e2e;
  margin-top: 13px;
  margin-bottom: 7px;
}
.completed-inner p {
  color: #04748e;
  font-size: 18px;
  font-weight: 400;
  line-height: 1;
}
.project-completed-size .swiper-button-prev::after,
.project-completed-size .swiper-button-next::after {
  color: #000;
}
.completed-img img {
  max-width: 180px;
  width: 100%;
}
.completedSwiper {
  padding: 0 30px;
}
.swiper-slide .completed-inner p {
  display: none;
}


@media (min-width: 1024px) and (max-width: 1300px) {
  .completed-inner h3 {
    font-size: 18px;
  }
  .completed-img {
    min-height: 115px;
  }
  .completed-inner {
    min-height: 235px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .completed-inner p {
    font-size: 16px;
    line-height: 1;
  }
}

@media (max-width: 1024px) {
  .completed-title h3 {
    font-size: 34px;
    line-height: 1.2;
  }
  .completedSwiper {
    padding: 0 15px;
  }
  section.project-completed-main {
    padding-right: 10px;
    padding-left: 10px;
  }
}

@media (max-width: 767px) {
  .project-completed-size .swiper-slide {
    width: initial !important; 
  }
  .completed-img {
    min-height: 140px;
  }
  .site-title-style h3 {
    font-size: 25px;
  }
  .completed-inner {
    min-height: 215px;
  }
  .completed-inner h3 {
    font-size: 16px;
    margin-top: 0;
    margin-bottom: 0px;
  }
  .completed-inner {
    min-height: 190px;
  }
}