.how-we-works-steps p {
  font-size: 16px;
  color: #292929;
  font-weight: 500;
  line-height: 25px;
  margin-top: 12px;
}

@media (min-width: 768px) and (max-width: 1200px) {
  .how-it-works-bottom.about-how-it-area {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-right: 10px;
    margin-left: 10px;
  }
  .how-it-works-bottom.about-how-it-area .how-it-works-steps {
    width: 32%;
    margin: 0 5px;
    margin-bottom: 25px;
    -ms-flex-preferred-size: 0;
    flex-basis: 1;
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    max-width: 100%;
  }
}

@media (max-width: 768px) {
  .how-it-works-bottom.about-how-it-area {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-right: 10px;
    margin-left: 10px;
  }
  .how-it-works-bottom.about-how-it-area .how-it-works-steps {
    width: 50%;
    margin: 0 5px;
    margin-bottom: 25px;
    -ms-flex-preferred-size: 0;
    flex-basis: 1;
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    max-width: 100%;
  }
}
