.row.plan-signle-inner {
  display: flex;
  justify-content: space-between;
  max-width: 1245px;
  margin: 0 auto;
  margin-bottom: 0 !important;
}
.row.plan-signle-inner .model-area-sn {
  width: 100%;
  max-width: 55%;
}
.row.plan-signle-inner .details-sn {
  width: 100%;
  max-width: 40%;
}
.row.plan-signle-inner .heading-plan {
  max-width: 100%;
  margin: auto;
  text-align: left;
}
.row.plan-signle-inner .heading-plan h1:after {
  margin: 0;
}
.row.plan-signle-inner .heading-plan h1 {
  margin-bottom: 30px;
}
.heading-plan h1 {
  font-size: 36px;
  font-weight: 900;
  letter-spacing: 1.5px;
  margin-top: 0;
  position: relative;
  margin-bottom: 65px;
  text-transform: capitalize;
}
.heading-plan h1:after {
  content: "";
  position: absolute;
  width: 110px;
  height: 3px;
  background: #ffa824;
  bottom: -15px;
  left: 0;
  right: 0;
  margin: 0 auto;
}
.planthum {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 9px;
}
.planthum a {
  display: block;
  width: 100%;
  max-width: 19.5%;
}
.planthum a img {
  width: 100%;
  height: 126px;
  object-fit: cover;
}
.mySwiper .swiper-slide {
  width: 25%;
  height: 100%;
  opacity: 0.4;
}
.mySwiper .swiper-slide-thumb-active {
  opacity: 1;
}
.plan-signle-inner  .swiper-button-prev:after,
.plan-signle-inner  .swiper-button-next:after {
  font-size: 15px;
}
.plan-signle-inner .swiper-button-prev, .plan-signle-inner .swiper-button-next {
  width: 35px;
  height: 35px;
  background: #fff;
  border-radius: 30px;
  box-shadow: 0 1px 5px 1px rgb(0 0 0 / 57%);
}
.popup-area .swiper-slide a {
  display: block;
  height: 445px;
}
.popup-area .swiper-slide a img {
  height: 100%;
  object-fit: cover;
}
.popup-area .planthum {
  height: 120px;
}
.popup-area .planthum img {
  height: 100%;
  object-fit: cover;
}
.cate-inner-top td p img {
  width: 18px;
  margin-right: 8px;
}
.cate-inner-top td p span {
  font-size: 18px;
  font-weight: 500;
}
.table {
  width: 100%;
  margin-bottom: 1rem;
  background-color: transparent;
  border-collapse: collapse;
}
.table-bordered td, .table-bordered th {
  border: 1px solid #dee2e6;
  box-sizing: border-box !important;
  border-collapse: collapse !important;
  padding: 12px 15px;
  text-align: left;
}
.table-bordered {
  border: 1px solid #dee2e6;
}
.cate-inner-top table {
  margin-top: 2rem;
}
.plan-signle-page .heading-plan p span {
  font-weight: bold;
  color: #ffa824;
}
.plan-signle-page .heading-plan p {
  font-size: 16px;
  line-height: 26px;
}
@media (max-width: 1024px) {
  .row.plan-signle-inner {
    align-items: center;
  }
  .row.plan-signle-inner {
    padding: 0 20px;
  }
  .form-plans {
    padding: 50px 15px;
  }
  .row.plan-signle-inner .model-area-sn {
    max-width: 66%;
  }
  .row.plan-signle-inner .details-sn {
    max-width: 28%;
  }
}

@media (max-width: 991px) {

.row.plan-signle-inner .model-area-sn {
  max-width: 100%;
}
.row.plan-signle-inner {
  flex-direction: column;
}
.row.plan-signle-inner .details-sn {
  max-width: 100%;
  padding: 4px;
}

}

@media (min-width: 600px) and (max-width: 889px) {
  .form-plans form .col-md-6 {
    width: 100%;
  }
}

@media (max-width: 600px) {
  .row.plan-signle-inner {
    align-items: center;
    display: block;
  }
  .row.plan-signle-inner .model-area-sn,
  .row.plan-signle-inner .details-sn {
    width: 100%;
  }
  .form-plans form .col-md-6 {
    width: 100%;
  }
  .form-plans form .row:last-child .col-md-12 {
    text-align: center;
  }
  .form-plans form button {
    font-size: 16x;
    max-width: 185px;
    padding: 17px 0;
  }
  .form-plans > h3 {
    margin-bottom: 2rem;
  }
  .planthum a img {
    height: 67px;
}

}
