.interior-bgImg {
    line-height: 0;
    background: #000;
}
.interior-bgImg img {
    opacity: 0.7;
}
.interior-bgImg img {
    width: 100%;
}
.interior-inner {
    position: relative;
}
.interior-content {
    position: absolute;
    top: 40%;
    width: 100%;
    text-align: center;
}
.interior-content h2 {
    margin: 0;
    color: #ffffff;
    font-size: 60px;
    font-weight: 500;
    line-height: 1;
}
.interior-content h3 {
    color: #fff;
    font-weight: 600;
    font-size: 70px;
    line-height: 1;
    margin: 0;
    margin-top: 4px;
}
.interior-content a {
    color: #fff;
    font-size: 18px;
    font-weight: 400;
    display: inline-block;
    margin-top: 20px;
}
.interior-main .swiper-button-prev::after, .interior-main .swiper-button-next::after {
    color: #fff;
}
@media (min-width: 1025px) and (max-width: 1440px){
    .interior-content h2 {
        font-size: 36px;
    }
    .interior-content h3 {
        font-size: 30px;
    }
}

@media (max-width: 1024px){
    .interior-bgImg img {
        height: 315px;
        object-fit: cover;
    }
    .interior-content {
        top: 33%;
    }
    .interior-content h2 {
        font-size: 34px;
    }
    .interior-content h3 {
        font-size: 24px;
      
    }
}