.services-banner-sec {
  line-height: 0;
}
.services-banner-sec img {
  max-width: 100%;
  width: 100%;
}
section.services-banner-main {
  border-top: 1px solid #ccc;
}

@media (max-width: 500px) {
  .services-banner-sec img {
    height: 150px;
    object-fit: cover;
    object-position: 0% 0;
  }
}
